import React from 'react'
import { BrowserRouter as Router } from "react-router-dom"
import Container from '../shared/template/Container'
import Header from '../shared/template/Header'
import SideNav from '../shared/template/SideNav'
import Toolbar from '../shared/template/Toolbar'
import Footer from '../shared/template/Footer'
import Routes from '../app/Routes'
import TopNav from '../shared/template/TopNav'
import Nav from '../shared/template/Nav'
import Row from '../shared/layout/Row'

const App = (props) => {
    return (
        <Container>
            <Header>  
                <img src="./img/BigDocesKLogo_26.png" alt="logo"/>
            </Header>
            <Toolbar>
                <div className="conteudo">
                 <img src="./img/redes-sociais/facebook.png" alt="facebook"/>
                 <img src="./img/redes-sociais/youtube.png" alt="youtube"/>
                 <img src="./img/redes-sociais/linkedin-logo.png" alt="linkedin"/>
                 <img src="./img/redes-sociais/twiter.png" alt="twiter"/>
                 </div>

            </Toolbar>
            <Router>
            <TopNav>
                <Nav></Nav>
            </TopNav>
            <SideNav>
                <Nav></Nav>
            </SideNav>
            <Routes></Routes>
            </Router>
            <Footer>
                <Row xright>
                <span>2021 © Lucas Developer - Big Sabor.</span>
                </Row>
            </Footer>
            <img className="whatsapp" src="./img/whatsapp-logo-4.png" alt="Whatsapp"/>

        </Container>
    )
}

export default App