import React from 'react'
import './dashboard.css'

const Dashboard = props => {
    return (
        <div className="body-1">

            <div className="hrs-atend">

              <h3 className="atendimento">HORÁRIO DE ATENDIMENTO</h3>
              <span className="dia">Segunda a Sabado</span>
              <span className="horarios">14h - 22hs</span>


           </div>


            <img src="./img/bolo-potes.png" alt=""/>
        
        <span className="span-1">A Tradicional Big Sabor nasceu da paixão pela gastronomia e 
            fascínio pela alquimia dos sabores. Ao longo da nossa história, 
            conquistamos os paladares mais exigentes e criamos receitas exclusivas 
            de bolos de festa, tortas salgadas e sobremesas cheias de personalidade
             e muito sabor, deixando manhãs mais animadas, festas mais saborosas
              e sobremesas de domingo com um gostinho mais especial. Hoje somos
              uma loja, resgatando valores intangíveis e nostálgicos presentes no lugar mais doce da 
              memória de nossos clientes. A Big Sabor é assim, muito mais que 
              uma casa de bolos, somos parte do sabor de cada bom momento do seu 
              dia a dia.</span>
              <h1>É um prazer fazer parte do seu dia!</h1>
              <img className="brigadeiro" src="./img/brigadeiroOriginal.png" alt="logotipo"/>
        </div>
    
    )
}

export default Dashboard